5,255,255,0.95);\n\n@popupDuration: 400ms;\n\n@actionsPopoverHairline: rgba(0,0,0,0.2);\n.modal-overlay, .preloader-indicator-overlay, .popup-overlay, .picker-modal-overlay, .toast-overlay {\n    position: absolute;\n    left: 0;\n    top: 0;\n    width: 100%;\n    height: 100%;\n    background: rgba(0,0,0,0.4);\n    z-index: 13000;\n    visibility: hidden;\n    opacity: 0;\n    .transition(@modalDuration);\n    &.not-animated {\n        .transition(0ms);\n    }\n    &.modal-overlay-visible {\n        visibility: visible;\n        opacity: 1;\n    }\n    &.transparent{\n      background: rgba(0,0,0,0);\n    }\n}\n.picker-modal-overlay {\n    z-index: 11000;\n}\n\n.popup-overlay {\n    z-index: 10500;\n}\n.modal-root {\n  position: absolute;\n  height: 100%;\n  width: 100%;\n  overflow-x: hidden;\n  top: 0;\n  left: 0;\n  z-index: 13500;\n}\n.modal {\n    width: 270px;\n    position: absolute;\n    z-index: 13500;\n    left: 50%;\n    margin-left: -135px;\n    margin-top: 0;\n    top: 50%;\n    text-align: center;\n    border-radius: 13px;\n    overflow: hidden;\n    opacity: 0;\n    .transform(translate3d(0,0,0) scale(1.185));\n    -webkit-transition-property: -webkit-transform, opacity;\n    -moz-transition-property: -moz-transform, opacity;\n    -ms-transition-property: -ms-transform, opacity;\n    -o-transition-property: -o-transform, opacity;\n    transition-property: transform, opacity;\n    color:#000;\n    display: none;\n\n    &.middle {\n      \n      .transform(translate3d(0,-50%,0) scale(1.185));\n    }\n\n    &.modal-in {\n        opacity: 1;\n        .transition(@modalDuration);\n        .transform(translate3d(0,0,0) scale(1));\n\n        &.middle {\n          .transform(translate3d(0,-50%,0) scale(1));\n        }\n    }\n    &.modal-out {\n        opacity: 0;\n        z-index: 13500-1;\n        .transition(@modalDuration);\n        .transform(translate3d(0,0,0) scale(1));\n    }\n}\n\n.modal-inner {\n    padding: 15px;\n    border-radius: 13px 13px 0 0